<template>
  <tr>
    <td>{{ no }}</td>
    <td>
      <router-link :to="`/read?no=${no}`">{{ title }}</router-link>
    </td>
    <td>{{ uid }}</td>
    <td>{{ getFormatDate(regdt) }}</td>
  </tr>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    no: { type: Number },
    uid: { type: String },
    title: { type: String },
    regdt: { type: String },
  },
  methods: {
    getFormatDate(regdt) {
      return moment(new Date(regdt)).format('YYYY.MM.DD');
    },
  },
};
</script>
