<template>
  <div>
    <table class="table table-borderless">
  
      <tr class = "title">
        <th>제목</th>
        <td>{{ item.title }}</td>
      </tr>
      <tr class = "second" style="border-bottom: 1px solid lightgray;">
        <th>작성자</th>
        <td>{{ item.uid }}</td>
        <th>등록일</th>
        <td>{{ getFormatDate(item.regdt) }}</td>
      </tr>
       
      <tr>
        <td colspan="2">
          {{ item.content }}
        </td>
      </tr>

    </table>

    <br />
    
    <div class="text-right">
      <router-link to="/list"><button class="btn btn-primary">목록</button></router-link>
      <router-link :to="'/update?no=' + item.no"><button class="btn btn-primary">수정</button></router-link>
      <router-link :to="'/delete?no=' + item.no"><button class="btn btn-primary">삭제</button></router-link>
   </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item']),
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
